// https://github.com/conorhastings/react-component-demo
export const ComponentCodeInteract = (props) => {
    const theme = useTheme();
    const { Component, name, componentProps } = props;
    const [cprops, setCprops] = useState(componentProps);
    const node = useRef(null)
    const codeString = `${Object.keys(cprops).reduce(
        (codeString, prop) => (codeString + `\n  ${prop}={${JSON.stringify(cprops[prop])}}`),
        `<${name}`
    )
        }
        \n/>`;

    const onKeyup = debounce(() => {
        const props = node.current.innerText.split('\n');
        const updatedProps = props.slice(1, props.length - 1).reduce((props, prop) => {
            let [key, value] = prop.trim().split("=");
            try {
                value = JSON.parse(value.substr(1, value.length - 2));
                props[key] = value;
            }
            catch (e) {
                //ignore
            }
            return props;
        }, {});
        setCprops(updatedProps);
    }, 1000)

    useEffect(() => {
        node.current.setAttribute('contentEditable', true);
        node.current.addEventListener('keydown', e => {
            if (e.keyCode === 13) {
                e.preventDefault();
                e.stopPropagation();
                return;
            }
        });
        node.current.addEventListener('keyup', onKeyup);
        // eslint-disable-next-line
    }, [])

    const handleCopyCode = () => {
        navigator.clipboard.writeText(node.current.innerText);
    };
    const [codeContainerOpen, setCodeContainerOpen] = useState(false);
    const handleCodeContainerOpen = () => {
        setCodeContainerOpen(!codeContainerOpen);
    }
    const MemoHighlighter = memo(() => (<SyntaxHighlighter
        language="jsx"
        style={dark}
        customStyle={{
            width: '100%',
            margin: 0,
            backgroundColor: blueGrey[900],
            border: '1px solid',
            borderColor: theme.palette.grey[800],
            borderRadius: 0,
            boxShadow: 0,

        }}
        wrapLongLines
        wrapLines={true}
    >
        {codeString}
    </SyntaxHighlighter>), [])

    return (
        <Box sx={{ display: 'flex', flexDirection: 'column' }}>
            <Component {...cprops} />
            {/* <CodeContainer codeString={codeString} onKeyup={onKeyup} ref={node} /> */}
            <Stack direction={'row'} justifyContent={'flex-end'} spacing={2}>
                <IconButton color="primary" aria-label="upload picture" component="label" onClick={handleCodeContainerOpen}>
                    <Code />
                </IconButton>
                <IconButton color="primary" aria-label="upload picture" component="label" onClick={handleCopyCode}>
                    <ContentCopy />
                </IconButton>
            </Stack>
            <Box ref={node} onKeyUp={onKeyup} sx={{
                display: codeContainerOpen ? 'block' : 'none',
                border: '1px solid',
                boxShadow: 0,
                margin: 0,
                borderColor: theme.palette.grey[600],
            }}>
                <MemoHighlighter />
            </Box>
        </Box >
    );
}

export default ComponentCodeInteract;